export const pagerQuery = graphql`
    fragment Thumbnail on ImageSharp {
        gatsbyImageData(
            width: 1280
            placeholder: BLURRED
            formats: [AUTO, WEBP]
            quality: 80
        )
    }
    fragment InterviewIcon on ImageSharp {
        gatsbyImageData(
            width: 240
            placeholder: BLURRED
            formats: [AUTO, WEBP]
            quality: 80
        )
    }
    fragment SnsIcon on ImageSharp {
        gatsbyImageData(
            width: 100
            placeholder: BLURRED
            formats: [AUTO, WEBP]
            quality: 100
        )
    }
`
export default IndexPage
